    background: url(https://raw.githubusercontent.com/CHTC/Articles/main/images/thyme/grid.png) center;
}

#hero-content-container {
    background-color: rgba(255, 255, 255, .9);
    border-radius: 5px;
    min-height: 300px;
    z-index: 0;
}

// Work with software-display.js to create the software display
#desktop-software-brief-container {
    min-height: 600px;
    background-color: #ececec;
}

.accordion-button:focus {
    z-index: 3;
    border-color: #a1a1a1;
    outline: 0;
    box-shadow: 0px 0px 0px !important;
}

.accordion-header {
    button.collapsed {
        background-color: white;
    }
}

.accordion-header {
    button {
        background-color: #DEDDDD;
    }
}

.desktop-software-brief {
    height:0px;
    overflow-y: hidden;
    transition-property: height;
    transition-duration: .25s;
    transition-timing-function: ease-out;
}

.desktop-software-brief.open {
    height: 100%;
}
